// Tamaños
$footer-alto: 60px;
$footer-tam-boletos-agregados: 80px;
$footer-alto-boletos-agregados: 110px;
$contenedor-menu: 64px;
$contenedor-principal: calc(100vh - #{$contenedor-menu} - #{$footer-alto} - 37px);
$contenedor-secundario-chico: calc(100vh - (#{$footer-alto} + #{$footer-tam-boletos-agregados} + 110px));
$contenedor-secundario: calc(100vh - (#{$footer-alto} + 75px));

// Colores
$primario: #15ab92;
$secundario: #FBE336;
$gris-oscuro: #777777;
$gris-ligero: #d8d8d8;
$riesgo: #F44336;
$blanco: #fff;
$negro: #000;

$naranja: #ff6961;
// Me gustó
$verdebaijito: #FFBF86;
$verdeApp: #01937C;
