/* Se utiliza para el contenido */
.content {
    min-height: $contenedor-principal;
    height: fit-content;
    height: --webkit-fit-content;
    height: --moz-fit-content;
    // background: #f0f2f5;

    // background: #f0f2f5;
    // margin: 15px;
    // padding: 15px;
    border-left-color: #d7dfe3;
    border-top-color: #d7dfe3;
    border-right-color: #d7dfe3;
    border-bottom-color: #d7dfe3;
    // border: 1px solid;
    border-radius: 5px;
    box-shadow: 0 5px 20px 0 rgb(0 0 0 / 5%);
}
.content-container {
    margin: 35px;
}

.container {
    // background: #FFF;
    padding: 15px;
    // border-left-color: #d7dfe3;
    // border-top-color: #d7dfe3;
    // border-right-color: #d7dfe3;
    // border-bottom-color: #d7dfe3;
    // border: 1px solid;
    border-radius: 10px;
    // box-shadow: 0 5px 20px 0 rgb(0 0 0 / 5%);
}

.footer {
    position: relative;
    bottom: 0;
    left: 0;
    padding: 16px 0;
    z-index: 10;
    // background-color: #7DC8BC;
}

.FooterContainer {
    margin-left: -15px;
    margin-right: -15px;
    padding-left: 30px;
    padding-right: 30px;
    background-color: var(--menu-fondo-sider);
    font-family: "CooperHewittBook", "CooperHewittBook", "CooperHewittBook" !important;
}

.FooterText {
    margin-bottom: -10px;
    padding-top: 6px;
    font-size: 8px;
    color: #fff;
}

@media (max-width:1279px){
    .footer{ margin-left: 0px }
}
@media (max-height: 550px){
    .footer{ top: 550px; }
    .content { height: 550px }
}
footer {
    padding: 0px !important;
}