.btn {
    cursor: pointer;
    font-size: 14px;
    // padding: 7px 10px 7px 10px;
    // transition: color .3s ease;
    min-width: 150px !important;
    // &:focus {
        // outline: none;
    // }
}
.btn-primario-circular {
    border-radius: 10px;
    background-color: $primario;
    border-radius: 2px;
    &:disabled {
        background-color: lighten($color: $primario, $amount: 20);
    }
    &:hover {
        background-color: darken($color: $primario, $amount: 10)
    }
}

.btn-primario {
    background-color: $primario !important;
    color: $blanco !important;
    &:disabled {
        background-color: $gris-ligero !important;
    }
}

.btn-advertencia {
    background-color: $naranja !important;
    color: $blanco !important;
    &:disabled {
        background-color: $gris-ligero !important;
    }
}

.btn-riesgo {
    background-color: $riesgo !important;
    color: $blanco !important;
    
    &:disabled {
        background-color: $gris-ligero !important;
    }
}

.btn-gris {
    background-color: $gris-oscuro !important;
    color: $blanco !important;
}

.btn-gris-ligero {
    background-color: $gris-ligero !important;
    color: $blanco !important;
}

.btn-icon-table {
    color: $gris-oscuro !important;
}
.btn-icon-table-disabled {
    // color: rgba(0, 0, 0, 0.26) !important;
    cursor: no-drop !important;
    // pointer-events: painted !important;
}

.bnt-cursor-no-crop {
    cursor: no-drop !important;
}