.flexCenterSpaceEvenly {
    display: flex;
    align-items: center;
    justify-content: space-evenly;
}

.btn-boleto-agregado {
    border-radius: 5px;
    width: 50px !important;
    height: 30px;
    svg {
        position: relative;
        top: 0px;
        color: $negro;
    }
}

.selecciona-boletos-sticky {
    position: sticky;
    top: 0;
    z-index: 5;
}

.encabezado-sticky {
    position: sticky;
}
.encabezado-sticky-01 {
    top: 0px;
    z-index: 5;
}
.encabezado-sticky-02 {
    top: 25px;
    z-index: 5;
}

// Se utiliza para la compra de boletos
.content-comprar-boletos {
    height: $contenedor-secundario-chico;
    // min-height: 200px;
    overflow: auto;
    // .MuiStepper-root {
        // background: $blanco;
    // }
}
.content-comprar-boletos-resumen-compra {
    // height: $contenedor-secundario;
    overflow: auto;
}
.content-comprar-boletos-resumen-compra-datos {
    padding-right: 15px !important;
    @media (max-width: 960px) {
        padding-right: 0px !important;
    }
}
.content-comprar-boletos-resumen-compra-tabla {
    height: calc($contenedor-secundario-chico - $footer-tam-boletos-agregados - $footer-alto - 100px);
    overflow: auto;
}
.content-comprar-boletos-selecciona {
    // background: $blanco;
    overflow: auto;
}
.content-boletos-agregados {
    position: relative;
    height: $footer-alto-boletos-agregados;
    overflow: auto;
    margin-right: 30px !important;
}
// Clase para los botones de los steps [siguiente y anterios] y se coloca arriba del footer
.content-btn-step {
    position: sticky;
    bottom: 0;
    // background: $blanco;
    // width: calc(100vw - 45px) !important;
    z-index: 5;
}
.content-btn-step-resumen {
    position: sticky;
    bottom: 0;
    // background: $blanco;
    // width: calc(100vw - 30px) !important;
    z-index: 5;
    // @media (max-width: 960px) {
    //     width: calc(100vw - 45px) !important;
    // }
}
// Fin compra de boletos